import React from "react"
import { DomainsResultsStyled } from "./DomainsResults.styled"
import Options from "./Options"
import Tlds from "./Tlds"
import Doms from "./Doms"
import { ShowLinkStyled, ColorsStyled, _, __ } from "./Domains.styled"
import { FontAwesomeIcon as FA } from "@fortawesome/react-fontawesome"
import Search from "../../components/Search"
import { faTimes, faStar as faStarSolid, faHeart as faHeartSolid } from "@fortawesome/pro-solid-svg-icons"
import { faBook, faAngleDown, faAngleUp, faCode } from "@fortawesome/pro-regular-svg-icons"
import WordPoss from "../../components/WordPoss"

const DomainsResults = function (props) {
  /*
   * User "container" state/props/methods!
   * This "child" component is to simplify container's render() logic.
   */
  let that = props.that
  /*
   * View variables:
   */
  // for results:
  let { suggestions_availability, input_str, input_tld, input_spellchecked } = that.props
  input_str = input_str.toLowerCase()
  let cue = []
  let original_domain = (input_str.replace(/ /g, "") + "." + input_tld)
  let original_code = suggestions_availability[original_domain] || 0
  // // get on the winning team...
  // if (input_spellchecked !== input_str) {
  //   let spelled = input_spellchecked.replace(/ /g, "") + "." + input_tld
  //   let spelled_code = suggestions_availability[spelled]
  //   if (original_code !== 1 && spelled_code == 1) {
  //     original_code = 1
  //     original_domain = spelled
  //   }
  // }
  /*
   * message about availability
   */
  if (!original_code) {
    cue.push(<span key="avai">Checking availability...</span>)
  } else if (original_code > 1 && original_code < 100) {
    cue.push(<span key="avai" className="color-white">"{original_domain}" is not available.</span>)
  } else if (original_code > 100 && original_code < 100000000) {
    cue.push(
      <span key="avai" className="color-attention">
        "{original_domain}" is being sold for ${Math.round(original_code).toLocaleString()}
      </span>
    )
  } else if (original_code > 100000000) {
    cue.push(
      <span key="avai" className="color-white">
        "{original_domain}" will expire on <b>{(new Date(original_code * 1000) + "").substring(4, 15)}</b>
      </span>
    )
  } else if (original_code == 1) {
    cue.push(
      <span key="avai" className="color-attention">
        <FA key={1} icon={faStarSolid} className="faStar" /> <b>{original_domain}</b> is available!
      </span>
    )
  } else {
    cue.push(
      <span key="avai">
        "{original_domain}" availability is "{original_code}"!
      </span>
    )
  }
  /*
   * message about spell check
   */
  if (input_spellchecked && input_spellchecked.replace(/ /g, "") !== input_str.replace(/ /g, "")) {
    cue.push(
      <div key="spellchecked" className="spellchecked">
        Including suggestions for "
        <b className="color-white">
          {input_spellchecked.split(" ").map((str, si) => (
            <span key={si}>
              {str}
              <__ />
            </span>
          ))}
          .{input_tld}
        </b>
        ".
        {/*<br /> To avoid spell-checking any word or phrase, put it in "quotes".*/}
      </div>
    )
  } else if (input_str.length > 20) {
    cue.push(
      <div key="spellchecked" className="spellchecked">
        For best results, add a space between words.
      </div>
    )
  }
  /*
   * show message
   */
  let cue_nav = (
    <ShowLinkStyled
      className="showThesaurus hide-small"
      onClick={() => {
        that.props.ui_actions.RX__toggle_poss()
      }}
    >
      {/*<span className="show-large">powered by:&nbsp; </span>*/}
      {/*<FA icon={faBook} className="faBook color-accent" style={{ transform: "scale(0.95)", opacity: "0.95" }} />*/}
      {/*&nbsp;&thinsp;*/}
      {!!that.props.show_poss ? (
        <>
          <FA icon={faTimes} className="toggleNLPThesaurus faBolt color-accent" />
          <span className="color-accent">&nbsp;HIDE word reference</span>
          <br />
          <a href="https://nlpthesaurus.com?str=hello" target="_blank">
            visit nlp
            <_ />
            thesaurus
            <_ />
            .com &raquo;
          </a>
        </>
      ) : (
        <>
          <FA icon={faBook} className="toggleNLPThesaurus faBolt color-accent" />
          &nbsp;NLP&thinsp;Thesaurus
        </>
      )}
    </ShowLinkStyled>
  )

  return (
    <DomainsResultsStyled>
      <div className="fixed_bg" />
      <Search
        {...that.props}
        className={"Search"}
        domains={true}
        placeholder={"enter two or three words"}
        title="Find an Available Domain Name:"
        title_nav={
          <a href="https://nlp.studio/#documentation" target="_blank" className="hide-small">
            <span className="color-subtle">get the API </span>
            <FA icon={faCode} className="faCode" />
          </a>
        }
        cue={cue}
        cue_nav={cue_nav}
      />

      {/*
       * POSS content, below Search form
       */}
      {!!that.props.show_poss && <WordPoss {...that.props} />}

      <div className="container">
        {/*
         * Show WIP
         */}
        {/*<ShowLinkStyled className="showWip">*/}
        {/*  {that.state.showWip ? (*/}
        {/*    <>*/}
        {/*      <span onClick={that.toggleWip} className="hide-small">*/}
        {/*        hide&nbsp;*/}
        {/*      </span>*/}
        {/*      WIP <FA icon={faAngleDown} />*/}
        {/*    </>*/}
        {/*  ) : (*/}
        {/*    <>*/}
        {/*      <span onClick={that.toggleWip} className="hide-small">*/}
        {/*        show&nbsp;*/}
        {/*      </span>*/}
        {/*      WIP <FA icon={faAngleUp} />*/}
        {/*    </>*/}
        {/*  )}*/}
        {/*</ShowLinkStyled>*/}

        {/*
         * Options
         */}
        <Options key="options" that={that} />

        {/*
         * Suggestions + TLDs + WIP
         */}
        <ColorsStyled
          className={"content results " + (!that.state.gotAvailability ? " gettingAvailability" : " gotAvailability")}
        >
          {/*
           * Results
           */}
          <Doms that={that} />
          <Tlds that={that} />
        </ColorsStyled>
      </div>
    </DomainsResultsStyled>
  )
}

export default DomainsResults
